import * as React from "react";
import { Component } from "react";

import { connect } from "react-redux";

import { StoreInterface } from "utils/types";
import { Dispatch } from "redux";
import { updateState } from "reduxStore/actions";

interface Props {
  gameWidth: number;
  currentEnemy: {
    name: string;
    srcName: string;
  };
  state: string;
  dispatch: Dispatch;
}

class Foreground extends Component<Props> {
  componentWillReceiveProps(nextProps: Props) {
    if (nextProps.state === "attack" || nextProps.state === "takeHit") {
      setTimeout(() => {
        this.props.dispatch(updateState("showQuestion"));
      }, 1500);
    }
  }

  render() {
    const numGrassBlocks = Math.ceil(this.props.gameWidth / 70);

    return (
      <div className="Foreground">
        <img
          className={`opponent player ${
            this.props.state === "takeHit" ? "hit" : ""
          }`}
          src="/static/img/character/body.png"
          alt="שחקן"
        />
        <img
          className={`opponent enemy ${
            this.props.state === "attack" ? "hit" : ""
          }`}
          src={`/static/img/${this.props.currentEnemy.srcName}/body.png`}
          alt="אויב"
        />
        <div className="grass">
          {[...Array(numGrassBlocks)].map((_, i) => {
            return <img src="/static/img/grass.png" alt="אדמה" key={i} />;
          })}
        </div>
        <style jsx>{`
          @keyframes hit {
            0% {
              background-color: unset;
              background-blend-mode: normal;
            }
            50% {
              background-color: rgba(200, 100, 0, 0.5);
              background-blend-mode: multiply;
            }

            100% {
              background-color: unset;
              background-blend-mode: normal;
            }
          }

          .Foreground {
            position: absolute;
            width: 100%;
            left: 0;
            bottom: 0;
          }

          .Foreground img.opponent {
            width: 10rem;
            height: 10rem;
            margin: 0 1rem -0.2rem;
          }

          .Foreground img.opponent.enemy {
            margin: 0 1rem -0.2rem;
            float: right;
          }

          .Foreground img.opponent.hit {
            animation: 1s hit running;
          }

          .Foreground .grass {
            width: 100%;
          }

          .Foreground .grass img {
            width: calc(100% / ${numGrassBlocks});
          }
        `}</style>
      </div>
    );
  }
}

function mapStateToProps(store: StoreInterface) {
  return { state: store.state };
}

export default connect(mapStateToProps)(Foreground);
